// app/components/MovieBanner.tsx
import { Movie } from '@/types';

interface MovieBannerProps {
  movie: Movie;
}

export default function MovieBanner({ movie }: MovieBannerProps) {
  return (
    <div className="relative h-[90vh]"> {/* Increased height to 90vh */}
      <div 
        style={{
          backgroundImage: `url(https://image.tmdb.org/t/p/original${movie.backdrop_path})`,
          backgroundSize: 'cover',
          backgroundPosition: 'top center'
        }}
        className="absolute inset-0"
      >
        <div className="absolute inset-0 bg-gradient-to-t from-black via-black/50 to-transparent">
          <div className="absolute bottom-32 left-8 max-w-3xl">
            <h1 className="text-white text-6xl font-bold mb-4">
              {movie.title}
            </h1>
            <p className="text-gray-200 text-lg mb-6">
              {movie.overview}
            </p>
            <div className="flex items-center gap-4">
              <div className="bg-amber-400 text-black px-4 py-1 rounded-full font-medium">
                IMDB: {movie.vote_average.toFixed(1)}
              </div>
              <div className="bg-white/20 text-white px-4 py-1 rounded-full">
                Released On: {new Date(movie.release_date).toLocaleDateString('en-US', {
                  month: 'short',
                  day: 'numeric'
                })}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
