// app/components/MovieList.tsx
import { Movie } from '@/types';

interface MovieListProps {
  movies: Movie[];
  onMovieSelect: (movie: Movie) => void;
}

export default function MovieList({ movies, onMovieSelect }: MovieListProps) {
  return (
    <div>
      <h2 className="text-white text-2xl font-semibold mb-4 px-8">Upcoming Movies</h2>
      {/* Added whitespace-nowrap to prevent wrapping and scrollbar-hide for clean scrolling */}
      <div className="flex overflow-x-auto scrollbar-hide whitespace-nowrap gap-4 px-8 pb-6">
        {movies.map((movie) => (
          <div 
            key={movie.id}
            onClick={() => onMovieSelect(movie)}
            className="inline-block cursor-pointer transition-transform hover:scale-105"
          >
            <img
              src={`https://image.tmdb.org/t/p/w500${movie.poster_path}`}
              alt={movie.title}
              className="w-[160px] h-[240px] rounded-lg object-cover"
            />
          </div>
        ))}
      </div>
    </div>
  );
}